.App {
    display: -ms-flexbox;
    display: flex;
    margin: 40px auto 0;
    max-width: 1000px;
    min-height: 500px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    color: var(--text-color);
    -webkit-box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
            box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
}

a {
    text-decoration: none;
    color: inherit;
}

.left, .right {
    background-size: cover;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}

.left {
    border-top-left-radius: var(--global-radius);
    border-bottom-left-radius: var(--global-radius);
    padding: 15px 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /*background-image: url("https://i.pinimg.com/564x/2c/78/bb/2c78bb1fd4b3d87391615cc22c1dd57f.jpg");*/
    -ms-flex: 1 1;
        flex: 1 1;
    border-right: solid 1px var(--accent-color);
}

.right {
    border-top-right-radius: var(--global-radius);
    border-bottom-right-radius: var(--global-radius);
    padding: 15px 15px 7px;
    -ms-flex: 2 1;
        flex: 2 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: -webkit-gradient(linear, left top, left bottom, from(#0b0f25), to(#655475));
    background: -webkit-linear-gradient(top, #0b0f25, #655475);
    background: -o-linear-gradient(top, #0b0f25, #655475);
    background: linear-gradient(to bottom, #0b0f25, #655475);
}

.footer {
    font-size: 0.8em;
    text-align: center;
    width: 100%;
}

.footer p {
    text-align: center;
    margin: 0;
}


